@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&family=Roboto:wght@400;500;700;900&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "roboto", sans-serif;
}

body {
  line-height: 1.5;
  background: url(images/mainbglower.jpg) center;
  background-size: cover;
  overflow-x: hidden;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  transition: 0.3s all ease-in-out;
}
nav.active {
  background-color: rgba(51, 51, 51, 0.3);
}
nav.active a {
  color: white;
}
nav.active .menu li:hover a {
  color: #00f1dd;
}
nav.active .menu li::after {
  background-color: #00f1dd;
}
nav.active .cart-items-container:hover,
nav.active .fa-bars:hover {
  color: #00f1dd;
}
nav.active .cart-items-container:hover .cart-number {
  color: #00f1dd;
  background-color: white;
}

nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
}
nav div {
  padding: 0.5rem 2.5rem;
}
.logo {
  width: 100px;
  cursor: pointer;
  transition: 0.3s all;
}
.logo:hover {
  transform: scale(1.1);
}
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 60px;
}
.menu li {
  margin: 1.5rem;
  font-family: "roboto", sans-serif;
  text-transform: uppercase;
  font-size: 0.9rem;
  font-weight: 500;
  display: inline-block;
  position: relative;
  cursor: pointer;
  transition: 0.3s all;
}
.menu a {
  text-decoration: none;
  color: #000;
}
.menu li:hover a {
  color: #02aa9b;
}
.menu li:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #02aa9b;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.menu li:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.cart-container {
  transform: 0.3s all;
  cursor: pointer;
  padding: 0;
  padding-right: 2.5rem;
  transition: 0.3s all;
}
.fa-cart-shopping {
  font-size: 1.6rem;
  transition: 0.3s all ease-in-out;
}
.cart-items-container {
  padding: 0;
  display: inline-block;
}
.cart-items-container:hover {
  color: #00f1dd;
}
.cart-items-container:hover .cart-number {
  background-color: white;
  color: #00f1dd;
}
.cart-number {
  padding: 0.2rem 0.4rem;
  background-color: red;
  border-radius: 100%;
  font-weight: 700;
  position: relative;
  top: -1.1rem;
  right: 0.5rem;
  font-family: "Montserrat", sans-serif;
  font-size: 0.8rem;
  transition: 0.3s all ease-in-out;
}
.fa-bars {
  font-size: 1.3rem;
  margin-left: 0.5rem;
  display: none;
  transition: 0.3s all ease-in-out;
}
.fa-bars:hover {
  color: #00f1dd;
}

.cart-page {
  position: fixed;
  top: 12.5%;
  left: 15%;
  border-radius: 10px;
  width: 70%;
  height: 75vh;
  z-index: 99;
  background-color: #333;
  border: 3px solid #e4e4e4;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2.5rem;
}
.cart-page h3 {
  align-self: flex-start;
  color: white;
  border-bottom: 2px solid #f4f4f4;
  width: 100%;
  padding-bottom: 1rem;
  font-weight: 700;
}
.cart-items {
  width: 100%;
  max-height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}
.cart-items .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.7rem;
  text-transform: uppercase;
  color: #d8d8d8;
  padding-top: 1rem;
  width: 100%;
}
.cart-items .header .span-1 {
  margin-right: 2.4rem;
}
.cart-items .cart-empty {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}
.cart-items .cart-empty p {
  margin: 0.5rem;
}
.cart-items .item {
  padding: 1rem 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart-items .item .title {
  width: 50%;
}
.cart-items .item .title .name {
  font-weight: 700;
  font-size: 0.9rem;
}
.cart-items .item .title .code {
  font-size: 0.8rem;
  color: #e4e4e4;
}
.cart-items .item div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cart-items .item .quantity {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 2rem;
}
.cart-items .item button {
  padding: 0.5rem 0.8rem;
  background: none;
  border: none;
  font-size: 1rem;
  color: #e4e4e4;
  cursor: pointer;
}
.cart-items .item .quantity button {
  font-size: 1.3rem;
}
.cart-items .item .quantity .decrement-btn {
  font-size: 2rem;
  padding: 0 0.8rem;
}
.cart-page .total-price {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3rem;
}
.cart-page .total-price .price {
  font-weight: 700;
}
.cart-page .cart-footer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  position: absolute;
  padding: 0 2rem;
  bottom: 1.5rem;
}
.cart-page .back-btn {
  font-size: 0.8rem;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.cart-page .back-btn i {
  margin-right: 0.5rem;
}
.cart-page .checkout-btn {
  background-color: #02aa9b;
  color: white;
  transition: 0.3s all ease-in-out;
  padding: 0.7rem 1.5rem;
  text-transform: uppercase;
  border: none;
  border-radius: 7px;
  cursor: pointer;
}
.cart-page .checkout-btn:hover {
  background-color: #29c2b5;
}

.burger-menu {
  min-height: 0;
  max-height: 0;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 99;
  transition: 0.5s all ease-in-out;
}
.burger-menu ul {
  list-style: none;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 50%;
}
.burger-menu a {
  text-decoration: none;
  color: black;
  font-weight: 700;
  font-size: 2rem;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.burger-menu li {
  transition: 0.3s all ease-in-out;
  margin: 1rem;
}
.burger-menu li:hover {
  transform: scale(1.2);
}
.burger-menu li:hover a {
  color: #02aa9b;
}
.burger-menu li:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: #02aa9b;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.burger-menu li:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.burger-menu .fa-xmark {
  cursor: pointer;
  color: black;
  display: none;
  font-size: 2rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  transition: 0.3s all ease-in-out;
}
.burger-menu .fa-xmark:hover {
  transform: scale(1.3);
  color: red;
  text-shadow: 1px 1px 3px gray;
}

.hero-container {
  height: 100vh;
  background: url(images/mainbgupper.jpg) center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.title {
  z-index: 1;
}

.title h1 {
  font-size: 4rem;
  font-weight: 900;
  text-transform: capitalize;
  line-height: 0.9;
  margin-top: 3rem;
  text-align: center;
  font-family: "montserrat", sans-serif;
}

.title span {
  color: #02aa9b;
}

.title h3 {
  text-align: center;
  margin: 3rem;
  font-weight: 600;
  font-size: 1rem;
  font-family: "montserrat", sans-serif;
}

