.discount-container {
  background-color: #333;
  padding: 3rem 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.discount-container p {
  color: white;
  font-size: 1.3rem;
}

.discount-container input {
  padding: 0.7rem;
  width: 250px;
  border-radius: 5px;
  margin-right: 0.5rem;
  border: none;
}

.discount-container button {
  padding: 0.7rem 1rem;
  background: #02aa9b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s all;
}

.discount-container button:hover {
  background-color: #29c2b5;
}

.products-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: 8rem auto;
}

.products-container div {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s all ease-in-out;
}

.fa-circle-chevron-right,
.fa-circle-chevron-left {
  color: #222;
  opacity: 0.5;
  font-size: 3.5rem;
  z-index: 5;
  cursor: pointer;
  transition: 0.1s all;
  position: absolute;
}

.fa-circle-chevron-left:hover,
.fa-circle-chevron-right:hover {
  opacity: 1;
}

.fa-circle-chevron-right:active,
.fa-circle-chevron-left:active {
  transform: scale(0.9);
}

.fa-circle-chevron-right {
  right: 3%;
  margin-right: -1rem;
}

.fa-circle-chevron-left {
  left: 3%;
  margin-left: -1rem;
}

.sellers-image-card,
.deals-image-card {
  width: 243px;
  height: 243px;
  background: url(images/sellers1.jpg) center;
  background-size: cover;
  margin: 0.2rem;
  border-radius: 3px;
  opacity: 0.5;
  transition: 0.3s all ease-in-out;
  cursor: pointer;
  position: relative;
}

.sellers-image-card p,
.deals-image-card p {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 0.9rem;
  background-color: #333;
  color: white;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  padding: 0 1rem;
  opacity: 0;
  max-height: 0;
  transition: 0.3s all ease-in-out;
}

.sellers-image-card:hover,
.sellers-image-card:focus,
.deals-image-card:hover,
.deals-image-card:focus {
  transform: scale(1.2);
  opacity: 1;
}

.sellers-image-card:focus p,
.sellers-image-card:hover p,
.deals-image-card:hover p,
.deals-image-card:focus p {
  max-height: 243px;
  padding: 1rem;
  opacity: 1;
}

.sellers-image-card span,
.deals-image-card span {
  display: block;
  font-size: 1.1rem;
  font-weight: 700;
}

