.products-container h3,
.testimonials-container h3 {
  font-family: "montserrat", sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.deals-container {
  margin-top: 10rem;
}

.btn-1 {
  padding: 0.8rem 1.7rem;
  border: #02aa9b 3px solid;
  background: none;
  border-radius: 30px;
  font-weight: 700;
  font-size: 0.9rem;
  margin-top: 2rem;
  cursor: pointer;
  transition: 0.3s all;
  text-decoration: none;
  color: black;
}

.btn-1:hover {
  color: white;
  background-color: #02aa9b;
}

.testimonials-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: auto;
  margin-bottom: 10rem;
}

.testimonial-cards-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonial-card {
  background: #fde3e8;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  margin: 0rem;
  width: 100%;
  min-height: 400px;
  opacity: 0.5;
  transition: 0.5s all ease-in-out;
  transform: scale(0.7);
}
.testimonial-card img {
  width: 120px;
  height: 120px;
  border-radius: 100%;
  object-fit: cover;
  object-position: 0 0;
}
.testimonial-card .img-exception {
  object-position: center;
}

.testimonial-card p {
  font-weight: 500;
  display: none;
  padding: 1rem;
  color: #333;
  font-size: 0.9rem;
}
.testimonial-card .name {
  font-weight: 900;
  font-size: 0.8rem;
  margin-top: 1rem;
}
.testimonial-card.active {
  background-color: #ffd7df;
  opacity: 1;
  transform: scale(1.2);
  margin-top: 3.5rem;
  z-index: 2;
  width: 100%;
  max-height: auto;
}
.testimonial-card.active p {
  display: block;
}
.testimonial-card.active img {
  width: 140px;
  height: 140px;
}

.testimonial-card.left {
  order: 1;
}
.testimonial-card.right {
  order: 3;
}
.testimonial-card.active {
  order: 2;
}

footer {
  background-color: #fff3f7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  color: #555;
  font-weight: 500;
}
.social-media-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.7rem;
}
.fa-brands {
  cursor: pointer;
  transition: 0.3s all;
  color: #333;
  font-size: 2rem;
}
.fa-square-twitter:hover {
  color: rgb(110, 198, 228);
}
.fa-square-facebook:hover {
  color: rgb(17, 85, 187);
}
.fa-square-instagram:hover {
  color: #ffc31d;
}
.fa-square-pinterest:hover {
