}

footer ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
footer li {
  margin: 1.5rem;
  color: #555;
  transition: 0.3s all;
  cursor: pointer;
  font-weight: 500;
}
footer a {
  text-decoration: none;
  color: #555;
  transition: 0.3s all;
  cursor: pointer;
  font-weight: 500;
}
footer a:hover,
footer li:hover {
  color: #000;
}
footer p {
  font-size: 0.9rem;
}

.contact-form {
  position: fixed;
  top: 5%;
  left: 15%;
  right: 0;
  bottom: 0;
  height: 91vh;
  width: 70%;
  background-color: white;
  color: black;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}
.contact-form-container {
  height: 100%;
}
.contact-form div {
  width: 100%;
  margin-bottom: 1rem;
}
.contact-form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contact-form-header p {
  font-size: 1.5rem;
}
.contact-form .fa-xmark {
  font-size: 1.3rem;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.contact-form .fa-xmark:hover {
  transform: scale(1.4);
  color: red;
}
.contact-form form {
  width: 100%;
}
.contact-form label {
  color: #111;
  font-size: 0.9rem;
}
.contact-form input,
.contact-form textarea {
  padding: 0.7rem;
  width: 100%;
  background-color: #fafafa;
  border: solid 1px lightgray;
}
.contact-form .name {
  margin-top: 2rem;
}
.contact-form .name label {
  width: 50%;
  display: inline-block;
}
.contact-form .name-inputs {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 0;
}
.contact-form #fn,
.contact-form #ln {
  font-size: 0.8rem;
}
.contact-form .submit-btn {
  width: 150px;
  background: #02aa9b;
  color: white;
  padding: 0.7rem 1rem;
  text-align: center;
  cursor: pointer;
  font-size: 1rem;
  transition: 0.3s all ease-in-out;
}
.contact-form .submit-btn:hover {
  background-color: #29c2b5;
}
.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 99;
  width: 100vw;
  height: 100vh;
  display: block;
}

.about-page {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
}

.about-page-container {
  width: 75%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 12px;
}
.about-page-container::after {
  content: url(images/pack1.png);
  position: absolute;
  right: -26.25rem;
  bottom: 18rem;
  transform: scale(0.8);
}
.about-page-container::before {
  content: url(images/pack2.png);
  position: absolute;
  left: -10rem;
  z-index: -1;
}
.about-page-container h2 {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 2.5rem;
  font-size: 1.8rem;
  width: 100%;
  font-weight: 600;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  background-color: #ffccd4;
}
.about-page-container .columns {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}
.about-page-container .columns div {
  margin: 0.5rem;
  background: #eee;
  width: 35%;
  min-width: 230px;
  height: 320px;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  font-size: clamp(0.85rem, 0.9rem, 1.15vw);
}
.about-page-container .slogan p {
  font-size: 1.3rem;
  font-weight: 500;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  display: flex;
  flex-direction: column;
